import React, { useState } from 'react';

const AddCommentForm = ({name, setArtInfo}) => {
  const [username, setUsername] = useState('');
  const [comment, setComment] = useState('');

  const addComment = async () => {
    const result = await fetch(`/api/articles/${name}/add-comment`, {
      method: 'post',
      body: JSON.stringify({ username, text: comment }),
      headers: {
        'Content-Type': 'application/json'
      }
    });

    const body = await result.json();
    console.log('BODY:', body)
    setArtInfo(body);
    setUsername('');
    setComment('');
  }

  return (
    <div id="add-comment-form">
      <h4>Add a Comment</h4>
      <label>
        Username:
        <input type="text" name="username" value={username} onChange={(e) => setUsername(e.target.value)} />
      </label>
      <label>
        Comment:
        <textarea rows="4" cols="50" name="comment" value={comment} onChange={(e) => setComment(e.target.value)} />
      </label>
      <button onClick={() => addComment()}>Add Comment</button>
    </div>
  )
}

export default AddCommentForm;