import React, { useState, useEffect } from 'react';
import articles from './article-content';
import ArticlesList from '../components/ArticlesList';
import PageNotFound from './NotFoundPage';
import CommentsList from '../components/CommentsList';
import UpvotesSection from '../components/UpvotesSection';
import AddCommentForm from '../components/AddCommentForm';

const ArticlePage = ({match}) => {
  const name = match.params.name;
  const article = articles.find(art => art.name === name);

  const [artInfo, setArtInfo] = useState({
    upvotes: 0,
    comments: []
  });

  useEffect(() => {

    const fetchData = (async () => {
      const result = await fetch(`/api/articles/${name}`);
      const body = await result.json();

      setArtInfo(body);
    });
    fetchData();
  }, [name]);

  if (!article) return <PageNotFound />;

  const content = article.content.map((p, i) => {
    return (<p key={i}>{p}</p>);
  })
  const filteredArts = articles.filter(art => art.name !== name);

  return (
    <>
      <h1>{article.title}</h1>
      <UpvotesSection name={name} upvotes={artInfo.upvotes} setArtInfo={setArtInfo}/>
      {content}
      <CommentsList comments={artInfo.comments} />
      <AddCommentForm name={name} setArtInfo={setArtInfo} />

      <h2>Other articles:</h2>
      <ArticlesList articles={filteredArts}/>
    </>
  )
}

export default ArticlePage;