import {Link} from 'react-router-dom';

const ArticlesList = ({articles}) => {
  return (
    articles.map((art, idx) => (
      <Link className="article-list-item" key={idx} to={`/article/${art.name}`}>
        <h3>{art.title}</h3>
        <p>{art.content[0].substring(0, 128)}...</p>
      </Link>
    ))
  )
}

export default ArticlesList;