import React from 'react';

const CommentsList = ({comments}) => (
  <>
    {comments.length ? <h3>Comments:</h3> : ''}
    {
      comments.map((c, i) => {
        return (
          <div className="comment" key={i}>
            <h4>{c.username}</h4>
            <p>{c.text}</p>
          </div>
        );
      })
    }
  </>
);

export default CommentsList;